define('aeonvera/initializers/error-handler', ['exports', 'ember', 'aeonvera/config/environment'], function (exports, Ember, ENV) {

  'use strict';

  var alreadyRun = false;

  var reportError = function reportError(errorData) {
    Ember['default'].$.ajax({
      url: 'https://aeonvera.com/api/front_end_error',
      method: 'POST',
      dataType: 'json',
      data: {
        error: errorData
      },
      success: function success() /*data, textStatus, jqXHR*/{
        // notify the user what happened, give link
        // similar to atom.io's editor

      },
      error: function error() /*jqXHR, textStatus, errorThrown*/{
        // not sure what to do if this fails... we can't report it
      }
    });
  };

  /*
    Adapter Error:
      errorData:
        - message
        - name
        - errors[]
          - detail (response from server)
          - status
          - title
  */
  var displayError = function displayError(errorData) {
    // debugger;

    var specific = '';
    if (errorData.errors) {
      specific = '(' + errorData.errors[0].status + ') ' + errorData.errors[0].title;
    }

    var message = '';
    if (errorData.message) {
      message = errorData.message;
    }

    var name = 'Error';
    if (errorData.name) {
      name = errorData.name + ': ';
    }

    var stack = '';
    if (errorData.stack) {
      stack = errorData.stack;
    }

    var errorBody = '' + '<span class="title"' + '<strong>' + name + '</strong>' + message + '</span>' +
    // '<a ' +
    '<hr>' + specific + '<br>' + stack;

    var id = Math.random().toString(36).replace(/[^a-z]+/g, '').substr(0, 64);
    var error = Ember['default'].$('<div/>', {
      id: 'error-' + id,
      'class': 'ember-error-notification',
      html: errorBody
    });
    var jError = Ember['default'].$(error);
    jError.appendTo('body');
    jError.slideDown(200);

    jError.click(function () {
      jError.remove();
    });

    setTimeout(function () {
      Ember['default'].$(error).remove();
    }, 15000);
  };

  exports['default'] = {
    name: 'error-handler',

    initialize: function initialize() {
      if (alreadyRun) {
        return;
      } else {
        alreadyRun = true;
      }

      Ember['default'].onerror = function (error) {
        Ember['default'].Logger.error(error);
        displayError(error);
      };
    }
  };

});